"use client";

import React from "react";
import levels from "../module/[id]/data/modules"; // ✅ Asegúrate de que la ruta es correcta
import ModuleCard from "../../progress/components/ModuleCard";
import UserProgress from "../../progress/components/UserProgress";
import ExamButton from "../../exams/components/ExamButton";
import LevelCompletionModal from "../../exams/components/LevelCompletionModal";
import { useState } from "react";

const ProgressPage: React.FC = () => {
  const [showModal, setShowModal] = useState(false);
  const [currentLevel, setCurrentLevel] = useState<"Principiante" | "Intermedio" | "Avanzado">("Principiante");
  const [isExamCompleted, setIsExamCompleted] = useState(false);

  const handleExamCompletion = (levelTitle: string) => {
    setCurrentLevel(levelTitle as "Principiante" | "Intermedio" | "Avanzado");
    setShowModal(true);
    setIsExamCompleted(true);
  };

  return (
    <div className="p-6">
      {/* 📌 Barra de progreso del usuario */}
      <UserProgress
        username="Camila Gómez"
        levels={[
          {
            id: 1,
            label: "Principiante",
            completedIcon: "/assets/level-1-completed.png",
            lockedIcon: undefined, // 🔹 El primer nivel no está bloqueado
            state: "completed",
          },
          {
            id: 2,
            label: "Intermedio",
            completedIcon: "/assets/level-2-completed.png",
            lockedIcon: "/assets/level-2-locked.png",
            state: "in-progress",
          },
          {
            id: 3,
            label: "Avanzado",
            completedIcon: "/assets/level-3-completed.png",
            lockedIcon: "/assets/level-3-locked.png",
            state: "locked",
          },
        ]}
      />

      {/* 📌 Niveles y módulos */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        {levels.map((level) => {
          const allModulesCompleted = level.modules.every((module) => module.status === "completed");

          return (
            <div key={level.id} className="flex flex-col gap-4">
              <h2 className="text-lg font-bold text-primary">{level.title}</h2>
              <p className="text-sm text-neutral-600">{level.subtitle}</p>

              {/* 🔹 Renderiza los módulos del nivel */}
              <div className="flex flex-col gap-2">
                {level.modules.map((module) => (
                  <ModuleCard
                    key={module.id}
                    id={module.id} // ✅ SE PASA EL ID CORRECTAMENTE
                    title={module.title}
                    status={module.status}
                  />
                ))}
              </div>

              {/* 📌 Botón para el examen del nivel */}
              <div className="mt-6 flex flex-col items-center">
                <p className="p16 text-neutral-600 mb-4">
                  {allModulesCompleted
                    ? `Avanza al examen del nivel ${level.title}`
                    : `Inicia el examen del nivel ${level.title}`}
                </p>
                <ExamButton
                  isDisabled={!allModulesCompleted || isExamCompleted}
                  onClick={() => handleExamCompletion(level.title)}
                >
                  Comenzar examen
                </ExamButton>
              </div>
            </div>
          );
        })}
      </div>

      {/* 📌 Modal de finalización de nivel */}
      {showModal && (
        <LevelCompletionModal
          level={currentLevel}
          username="Camila Gómez"
          onClose={() => setShowModal(false)}
        />
      )}
    </div>
  );
};

export default ProgressPage;
