"use client";

import { useState } from "react";
import ExamModal from "./ExamModal";

interface ExamButtonProps {
  isDisabled: boolean;
  moduleId: number; // 🔹 Agregamos moduleId para saber qué examen abrir
  children: React.ReactNode;
}

const ExamButton: React.FC<ExamButtonProps> = ({ isDisabled, moduleId, children }) => {
  const [isExamOpen, setIsExamOpen] = useState(false);

  return (
    <>
      <button
        disabled={isDisabled}
        onClick={() => setIsExamOpen(true)} // 🔹 Abre el modal al hacer clic
        className={`primary-button ${
          isDisabled ? "bg-gray-400 cursor-not-allowed" : "bg-primary hover:bg-primary-dark text-white"
        }`}
      >
        {children}
      </button>

      {/* 📌 Renderizamos el modal solo si está abierto */}
      {isExamOpen && <ExamModal isOpen={isExamOpen} onClose={() => setIsExamOpen(false)} moduleId={moduleId} />}
    </>
  );
};

export default ExamButton;
